header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  height: 50px;
  width: 100%;
  background-color: #212121;
  z-index: 5; }

.burger {
  display: none; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  &_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    &:hover {
      color: $main-color;
      text-decoration: none; } }

  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:last-child {
      border-right: none; } } }

.subheader {
  position: absolute;
  top: 50px;
  height: 84px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);

  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  &_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  &_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  &_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 10px;
    &:hover {
      color: $main-color;
      text-decoration: none; } }

  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    border-radius: 4px;
    @include btn_hover; } }

.promo {
  min-height: 800px;
  background: url('../img/first_bg.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  &_header {
    color: #ffffff;
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  &_subheader {
    color: #ffffff;
    font-size: 36px;
    font-weight: 400;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  &_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    margin-top: 23px;
    text-align: center; }

  &_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

.section_header {
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  margin-top: 38px; }

.section_subheader {
  color: #222222;
  font-size: 17px;
  font-weight: 400;
  text-align: center;
  margin-top: 19px; }

.rectangle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 112px;
  height: 24px;
  background-color: #000000;

  &_subheader {
    color: #ffffff;
    font-size: 11px;
    font-weight: 400;
    margin-top: 8px;
    text-align: center; } }

.advantages {
  min-height: 900px;
  padding: 71px 0 65px 0;

  &_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px;

    .circle {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 26px;
      width: 113px;
      height: 113px;
      background-color: #1eacc7;
      border-radius: 100%; } }

  &_text {
    width: 343px; }

  &_subheader {
    color: #1eacc7;
    font-size: 17px;
    font-weight: 400;
    margin-top: 40px; }

  &_descr {
    color: #202020;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px;
    text-align: justify; } }

.mobile {
  min-height: 864px;
  padding: 48px 0 80px 0;
  background: url('../img/possibilities_bg.png') center (center / cover) no-repeat;
  .rectangle_subheader {
    position: relative;
    color: white;
    z-index: 2;
    text-transform: uppercase; }
  .row {
    margin-top: 31px; }
  &_item {
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    position: relative;
    &_1 {
      background: url('../img/advantage_1.jpg') center (center / cover) no-repeat; }
    &_2 {
      background: url('../img/advantage_2.jpg') center (center / cover) no-repeat; }
    &_3 {
      background: url('../img/advantage_3.jpg') center (center / cover) no-repeat; }
    &_4 {
      background: url('../img/advantage_4.jpg') center (center / cover) no-repeat; }
    &_5 {
      background: url('../img/advantage_5.jpg') center (center / cover) no-repeat; }
    &_6 {
      background: url('../img/advantage_6.jpg') center (center / cover) no-repeat; }
    &_subtitle {
      color: #ffffff;
      font-size: 24px;
      font-weight: 400;
      text-transform: uppercase;
      text-align: center; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      width: 26px;
      height: 26px;
      background-color: #000000;
      margin: 0 auto;
      border-radius: 100%;
      cursor: pointer;

      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $main-color;
        transition: 0.3s all; }

      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: $main-color;
        transition: 0.3s all; }

      &:hover {
        &:before, &:after {
          background-color: $second-color; } } } } }

.choice {
  position: relative;
  min-height: 896px;
  padding: 57px 0 138px 0;
  background: url('../img/car_bg.png') center (center);
  .rectangle_white {
    position: relative;
    width: 112px;
    height: 24px;
    background-color: #ffffff;
    z-index: 2; }
  .rectangle_subheader {
    position: relative;
    color: #070716;
    z-index: 2;
    text-transform: uppercase; }
  .section_header_white {
    margin-top: 32px;
    position: relative;
    color: #ffffff;
    z-index: 2; }
  .section_subheader_white {
    margin-top: 8px;
    position: relative;
    color: #ffffff;
    z-index: 2;
    text-transform: uppercase; }
  .choice_img {
    position: relative;
    display: block;
    margin: 56px auto 0 auto;
    z-index: 2; }
  .choice_descr {
    color: #222222;
    font-size: 17px;
    font-weight: 300;
    text-align: center;
    z-index: 2; }
  .black_bg {
    position: absolute;
    top: 0;
    left: 0;
    height: 499px;
    width: 100%;
    background-color: #070716;
    z-index: 1; } }

.conditions {
  padding: 68px 0 84px 0;

  .rectangle_subheader {
    position: relative;
    color: white;
    z-index: 2;
    text-transform: uppercase; }

  &_block {
    margin: 33px 0;
    &_nmb {
      margin-bottom: 0px; } }

  &_title {
    color: #1ca8c3;
    font-size: 22px;
    font-weight: 400; }

  &_list {
    list-style-type: none;
    padding-left: 50px;
    margin-top: 22px;
    li {
      position: relative;
      color: #222222;
      font-size: 17px;
      font-weight: 300;
      line-height: 24px;
      margin-bottom: 10px;
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: -32px;
        top: 3px;
        width: 21px;
        height: 20px;
        background: url('../icons/check_blue.png'); } } }

  &_descr {
    color: #1ca8c3;
    font-weight: 300;
    margin-bottom: 62px; }

  .warning {
    .conditions_title {
      color: $second-color; }
    .conditions_list {
      li {
         &:before {
          background: url('../icons/Check_red.png'); } } } } }

.world {
  min-height: 717px;
  background: url('../img/footage_bg.jpg') center (center / cover) no-repeat;
  padding: 49px 0;

  .rectangle_white {
    position: relative;
    width: 112px;
    height: 24px;
    background-color: #ffffff; }

  .rectangle_subheader {
    position: relative;
    color: #070716;
    text-transform: uppercase; }

  .section_header_white {
    color: #ffffff; }

  .section_subheader_white {
    color: #ffffff; }

  &_link {
    display: block;
    color: #1fbad6;
    font-size: 15px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    margin-top: 486px;
    &:hover {
      color: white;
      text-decoration: none; } } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }

.xs_visible {
  display: none; }
